import { FC } from 'react';
import { clsx } from 'clsx';

import { CardScope, CardProps } from './card.interface';

export const Card: FC<CardProps> = ({ children, image, ...props }) => {
  const padding = image ? 'pt-0 px-0' : 'px-zep-1.5 py-zep-2';

  return (
    <div
      className={clsx(
        CardScope,
        padding,
        'outline-none',
        'h-full',
        'flex',
        'flex-col',
        'flex-nowrap',
        'items-start',
        'bg-white',
        'rounded-zep-largeElement',
        'drop-shadow-zep-default',
        'hover:drop-shadow-zep-hover',
        '[&>img]:rounded-tl-lg',
        '[&>img]:rounded-tr-lg',
      )}
      tabIndex={0}
      data-testid="zep-card"
      {...props}
    >
      {image}
      {image ? (
        <div className="px-zep-1.5 py-zep-2 flex flex-col flex-nowrap items-start h-full">{children}</div>
      ) : (
        children
      )}
    </div>
  );
};
